/* pt-mono-regular - latin */
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-mono-v8-cyrillic-ext-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("../fonts/pt-mono-v8-cyrillic-ext-regular.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("../fonts/pt-mono-v8-cyrillic-ext-regular.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/pt-mono-v8-cyrillic-ext-regular.woff") format("woff"), /* Modern Browsers */
    url("../fonts/pt-mono-v8-cyrillic-ext-regular.ttf") format("truetype"), /* Safari, Android, iOS */
    url("../fonts/pt-mono-v8-cyrillic-ext-regular.svg#PTMono") format("svg"); /* Legacy iOS */
}

/*Roboto Medium*/
@font-face {
  font-family: "Roboto Medium";
  src: url("../fonts/roboto-medium.eot"); /* IE9 Compat Modes */
  src: url("../fonts/roboto-medium.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("../fonts/roboto-medium.otf") format("opentype"), /* Open Type Font */
    url("../fonts/roboto-medium.svg") format("svg"), /* Legacy iOS */
    url("../fonts/roboto-medium.ttf") format("truetype"), /* Safari, Android, iOS */
    url("../fonts/roboto-medium.woff") format("woff"), /* Modern Browsers */
    url("../fonts/roboto-medium.woff2") format("woff2"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

