* {
  box-sizing: border-box; }

body {
  font-family: "PT Mono", "Arial", monospace;
  font-size: 16px;
  font-weight: normal;
  line-height: 22px;
  margin: 0;
  padding: 0;
  color: #ffffff;
  background-color: #1B2D37;
  background-image: url("../img/body-background.png");
  background-position: top left;
  background-repeat: no-repeat;
  background-size: 100%; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.page {
  height: 100%; }

.body {
  min-height: 100%;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  align-content: start; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.main-header {
  width: 75%;
  margin: auto; }
  @media (max-width: 1023px) {
    .main-header {
      width: 96%; } }

.logo {
  width: 15%;
  display: flex;
  margin-top: 33px;
  margin-left: 13px; }
  @media (max-width: 1023px) {
    .logo {
      width: 28%;
      margin-top: 20px;
      margin-left: 10px; } }
  @media (max-width: 767px) {
    .logo {
      width: 12.3%;
      margin-top: 10px;
      margin-left: 11px; } }

.logo__image-second {
  margin-top: 10px;
  margin-left: 16px; }
  @media (max-width: 767px) {
    .logo__image-second {
      display: none; } }

.contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: 2.71%;
  display: flex;
  flex-wrap: wrap;
  width: 45%;
  justify-content: flex-end;
  align-content: center; }
  @media (max-width: 1023px) {
    .contacts-list {
      margin-right: 0; } }

.contacts-list__item {
  margin: 0;
  padding: 0;
  margin-top: 17px; }
  @media (max-width: 1023px) {
    .contacts-list__item {
      margin-top: 0; } }

@media (max-width: 1023px) {
  .contacts-list__item:first-child {
    margin-right: 14px; } }

@media (max-width: 767px) {
  .contacts-list__item:first-child {
    margin-right: 3px; } }

.contacts-list__link {
  display: flex;
  justify-content: flex-start;
  margin: 0;
  padding: 0;
  margin-top: 18px;
  padding: 10px;
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 0.83vw;
  line-height: 18px;
  color: #ffffff; }
  @media (max-width: 767px) {
    .contacts-list__link {
      margin-top: 8px; } }
  .contacts-list__link:hover {
    color: #00B2FF; }
    .contacts-list__link:hover .contacts-list__mail-image,
    .contacts-list__link:hover .contacts-list__phone-image {
      fill: #ffffff; }

.contacts-list__link span {
  margin-left: 16px; }
  @media (max-width: 1023px) {
    .contacts-list__link span {
      display: none; } }

.contacts-list__link--margin {
  margin-right: 70px; }
  @media (max-width: 1023px) {
    .contacts-list__link--margin {
      margin-right: 0; } }
  .contacts-list__link--margin span {
    margin-left: 20px; }

.contacts-list__mail-image {
  fill: #00B2FF; }
  @media (max-width: 1023px) {
    .contacts-list__mail-image {
      width: 26px;
      height: 19px; } }

.contacts-list__phone-image {
  fill: #00B2FF; }
  @media (max-width: 1023px) {
    .contacts-list__phone-image {
      width: 23px;
      height: 23px; } }

.order-call {
  align-self: center;
  margin: 0;
  padding: 0;
  margin-top: 32px;
  margin-right: 16px;
  padding: 14px;
  width: 180px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 5px; }
  @media (max-width: 1023px) {
    .order-call {
      display: none; } }
  .order-call:hover {
    color: #00B2FF;
    border-color: #00B2FF; }
